import JQ from "jquery";

export default {
    errorPosiFlag:false,
    nl2br(text) {
        return text.value.replace(/(\n|\r\n)/g, '<br>');

    },
    nl2brContent(text) {
        text = text.toString();
        text = text.replace(/(\n|\r\n)/g, '<br>');
        return text.replace(/(<br><br>)/g, '<br>');
    },
    stripTags(value) {
        let div = document.createElement("div");
        div.innerHTML = value;
        let text = div.textContent || div.innerText || "";

        return text;

    },
    stripTagsNotEm(value) {
        let div = document.createElement("div");
        div.innerHTML = value.replace(/<em>/ig,'{em}').replace(/<\/em>/ig,'{/em}')
        let text = div.textContent || div.innerText || "";
        let result = text.replace(/{em}/ig,'<em>').replace(/{\/em}/ig,'</em>')
        return result;

    },
    email(value) {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value)
    },
    numericByPrice(value) {
        if(value==0)return false;
        if(value) {

            value = value.toString()
            let targetPrice = value.replace(/,/gi, '');
            targetPrice = targetPrice.toString().replace(/-/gi, '');

            return isNaN(targetPrice);
        } else return true;
    },
    numeric(value) {
        return isNaN(value);
    },
    parseNumber(value) {
        if(value) {
            value = value.toString()
            let number = value.replace(/,/gi, '');
            number = number.toString().replace(/-/gi, '');
            return parseInt(number);

        } else return 0;
    },
    price(value) {
        if(value) {
            value = value.toString()
            let targetPrice = value.replace(/,/gi, '');
            targetPrice = targetPrice.toString().replace(/-/gi, '');

            if (!this.numeric(targetPrice)) {
                return this.numberFormat(targetPrice);

            } else return false;
        } else return false;
    },
    br2nl(text) {
        return text.replace(/(<br>|<br\/>|<br \/>)/g, '\r');
    },
    numberFormat(number) {
        if(!number)return 0;
        if(isNaN(number)) {
            number = number.toString().replace(/-/gi, '');
        }
        let nf = new Intl.NumberFormat(["en-US"], {});
        return nf.format(number);
    },
    fNumberFormat(number) {
        number = number.toString().replace(/-/gi, '');
        number = number.toString().replace(/,/gi, '');
        let nf = new Intl.NumberFormat(["en-US"], {});
        return nf.format(number);
    },
    getStrCut(string,length) {
        if(string.length >= length){
            return string.substr(0,length)+"..";
        } else return string;
    },
    errorPosition(baseEle,targetEle,func) {
        const baseElement = (baseEle=='base')?JQ('body'):JQ(baseEle);
        const height = JQ(baseElement).height();

        const top = JQ(targetEle).offset().top;

        const stTop = (top>0)?height - top + 200:height + top - 200;
        if(baseEle=='base') {

            JQ('html,body').animate({
                scrollTop: (top - 50)
            }, 100, () => {
                if(this.errorPosiFlag) {
                    this.errorPosiFlag = false;
                    return
                }
                func();
                this.errorPosiFlag = true;
            })
        } else {
            JQ(baseElement).animate({
                scrollTop:stTop

            }, 100, () => {
                func();
            })
        }
    },

}
