<template>
    <div :class="'board-container '+compontentType+' '+mobileClass">
        <div class="modal-header">
            <div class="center-title">
                {{title}}
            </div>
            <div class="del-box" @click="close()">
                <font-awesome-icon icon="fas fa-times"></font-awesome-icon>
            </div>
        </div>
        <form novalidate @submit.prevent="validate">
            <div :class="'contents '+mobileClass">
                <div class="from-box">
                    <ul class="field-box">
                        <li style="padding-top:20px;">
                            <div class="required-icon">제목</div>
                            <div class="field-input">
                                <v-text-field
                                    v-model="subject"
                                    required
                                    background-color="#fff"
                                    placeholder="제목을 입력하세요"
                                    :error-messages="subjectErrors"
                                    @input="$v.subject.$touch()"
                                    @blur="$v.subject.$touch()"
                                    outlined
                                    :dense="dense"
                                ></v-text-field>
                            </div>
                        </li>
                        <li>
                            <div class="required-icon">내용</div>
                            <div class="field-input">
                                <v-textarea
                                    outlined
                                    background-color="#fff"
                                    placeholder="내용을 입력하세요"
                                    v-model="description"
                                    :error-messages="descriptionErrors"
                                    @input="$v.description.$touch()"
                                    @blur="$v.description.$touch()"
                                    rows="10"

                                ></v-textarea>
                            </div>
                        </li>
                        <li v-if="ufileView">
                            <div class="required-icon">파일 업로드</div>
                            <div class="field-input">
                                <div class="file-box">
                                    <div class="file-names" style="background:#fff;">
                                        {{fileName}}

                                    </div>
                                    <div class="file-click">
                                        <input ref="imgContainer" @change="onChangeImg" type="file"
                                               id="vfile">
                                        파일 찾기
                                    </div>
                                </div>
                                <div class="file-message">&#8251; {{limitFileSize/(1024*1024)}}MB 이하로 제한됩니다</div>

                            </div>
                        </li>
                        <li v-if="noticeView">
                            <div class="required-icon">공지글 지정</div>
                            <div class="field-input">
                                <ul class="switch-ul">
                                    <li @click="setNotice('yes')" :class="noticeClass.yes"> 예</li>
                                    <li @click="setNotice('no')" :class="noticeClass.no"> 아니오</li>
                                </ul>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div :class="'modal-footer '+mobileClass">
                <p class="submit-button-box">
                    <v-btn
                        type="button"
                        class="cancle-button"
                        @click="close()"
                        v-if="!mobileView"
                    >
                        취소
                    </v-btn>
                    <v-btn
                        type="submit"
                        class="submit-button"
                    >
                        확인
                    </v-btn>
                </p>
            </div>
        </form>

    </div>
</template>


<script>
	import {validationMixin} from 'vuelidate'
	import {required} from 'vuelidate/lib/validators'
    import UTIL from "@/common/util";
    import AUTH from "@/config/web/auth"
    import {
        API_URL_BROADCAST_LIVE_BOARD_ARTICLE_INFO,
        API_URL_BROADCAST_LIVE_BOARD_ARTICLE_INSERT,
        API_URL_BROADCAST_LIVE_BOARD_ARTICLE_UPDATE,
        API_URL_BROADCAST_LIVE_BOARD_ARTICLE_FILE_UPLOAD
    } from "@/config/web/urls";
	import apiService from "@/config/web/apiService";

	export default {
		name: 'liveBoardRegist',
		mixins: [validationMixin],
		validations: {
			subject: {required},
			description: {required},

		},
		props: ["parentId", "no", "compontentType","userInfo"],
		data() {
			return {
				insertFlag: false,
				mobileView: false,
				mobileClass: '',
				title: '게시글 작성',
				subject: '',
				description: '',
				notice: 'yes',
				fileName: '',
				profileImg: '',
				fileExt: '',
				noticeView: false,
				uid: '',
				userName: '',
				limitFileSize: 1048576,
				noticeClass: {
					yes: 'active',
					no: ''
				},
				ufileAuth: 'host',
				ufileView: false,
                dense:false,

			}
		},
		created() {
            if (this.$isMobile()) {
                this.mobileClass = 'mobile'
                this.mobileView = true;
                this.dense = true;
            }
           let memberInfo = AUTH.getSession(this.$isMobile());
            if (memberInfo && memberInfo?.memberInfo) {
                this.uid = memberInfo.memberInfo.uid;
		    }
        	this.getData();
		},
		computed: {
			subjectErrors() {
				const errors = []
				if (!this.$v.subject.$dirty) return errors
				if (!this.$v.subject.required) {
					errors.push('제목을 입력하세요')
				}
				return errors
			},
			descriptionErrors() {
				const errors = []
				if (!this.$v.description.$dirty) return errors
				if (!this.$v.description.required) {
					errors.push('내용을 입력하세요')
				}
				return errors
			},

		},
		methods: {
			getData() {
				let frm = {
                    parentId: this.parentId.toString()
                }
             	if (this.no != 'new' && this.no) {
					frm.no =  this.no.toString();
				}
				apiService.post(API_URL_BROADCAST_LIVE_BOARD_ARTICLE_INFO, frm, (ret) => {
					if (this.uid == 'admin') {
						this.noticeView = true;
						this.notice = 'yes';
					} else this.notice = 'no';
					if (ret.boardInfo) {
						if (this.notice == 'no' && this.uid == ret.boardInfo.uid) {
							this.noticeView = true;
							this.notice = 'yes';
						}

						this.ufileAuth = ret.boardInfo.ufile_auth;
                        /*
						if (ret.boardInfo.limitRegDate) {
							let limiDates = ret.boardInfo.limitRegDate.split('T');
							let limiDate = limiDates[0] + ' 23:59:59';
							let fendate = new Date(limiDate);
							let nowDate = new Date();
							if (fendate.getTime() >= nowDate.getTime()) {
								this.limitFileSize = 1024 * 1024 * ret.boardInfo.limitFileSize;

							}
						}
                         */
					}
					switch (this.ufileAuth) {
						case 'host':
							if (this.noticeView) this.ufileView = true;
						case 'user':
							if (this.uid) {
								this.ufileView = true;
							}
						case 'all':
							this.ufileView = true;
							break;

					}
					if (this.no != 'new' && this.no) {
					    if(ret.boardArticleInfo && ret.boardArticleInfo?.status=='success') {
					        this.subject = ret.boardArticleInfo.info.subject;
                            this.description = UTIL.br2nl(ret.boardArticleInfo.info.content);
                            this.setNotice(ret.boardArticleInfo.info.notice)
                            this.fileName = ret.boardArticleInfo.info.fileName;

                        }

					}
				});
			},
			onChangeImg() {
				const imgObj = this.$refs.imgContainer.files;
				let img = imgObj.length > 0 ? imgObj[0] : null;
				this.fileName = img.name;

                const frm = new FormData();
                frm.append('file', img);
                apiService.postFile(API_URL_BROADCAST_LIVE_BOARD_ARTICLE_FILE_UPLOAD, frm, (res) => {
                    if (res.status == 'success') {
                        this.profileImg = res.file;
                    }
                })
                this.$refs.imgContainer.value = '';


			},
			setNotice(cmd) {
				this.notice = cmd;
				let yesActive = '';
				let noActive = '';
				switch (cmd) {
					case 'yes':
						yesActive = 'active';
						break;
					case 'no':
						noActive = 'active';
						break;
				}

				this.noticeClass = {
					yes: yesActive,
					no: noActive
				};
			},
			close() {
				if(this.compontentType=='live') {
					this.$emit("hideEvent",{page:'regist'});
                } else {
					this.$emit("modalHIde",'none');
				}
			},
			validate() {
				this.$v.$touch()
				if (!this.$v.$anyError) {
					this.formSubmit();
				}
			},
			formSubmit() {
				if (this.insertFlag) return;
				this.insertFlag = true;

				let frm = {
				    parentId:this.parentId.toString(),
                    subject: this.subject,
                    content: this.description,
                    notice: this.notice
                }
                if(this.userInfo?.uid) {
                    frm.uid = this.userInfo.uid;
                    frm.userName = this.userInfo.userName;
                }
                let url = API_URL_BROADCAST_LIVE_BOARD_ARTICLE_INSERT;
				if (this.no != 'new' && this.no) {
				    frm.no = this.no.toString();
                    url = API_URL_BROADCAST_LIVE_BOARD_ARTICLE_UPDATE;
                }

				if (this.profileImg) {
					frm.fileUrl = this.profileImg;
					frm.fileName = this.fileName;
				}

				this.$eventBus.$emit('overlay', 'show');
				apiService.post(url, frm, (res) => {
					this.$eventBus.$emit('overlay', 'hide');
					let params = {}
					this.insertFlag = false;
					params = {
						message: '정보가 저장되었습니다',
						doAction: () => {
							if(this.compontentType=='live') {
								this.$emit("hideEvent",{page:'regist'});
							} else {
								this.$emit("modalHIde",'update');
							}
						}
					};
					this.$eventBus.$emit('modalOpen', params);


				})
			},
		},

	}
</script>
<style lang="scss" scoped>
    .board-container {
        min-height: auto;
        background: #fff;
        input:focus {
            outline: none !important;

        }

        ::v-deep input:focus {
            outline: none !important;
        }

        &.mobile {
            background: #fff;
            width: calc(100vw - 40px);
            height: calc(100vh - 40px);
            left: 20px;
            top: 20px;
            padding:0;
            overflow: hidden;
            position: relative;
            .modal-header {
                padding:12px 15px;
                .center-title {
                    font-size: 12pt;
                }
                .del-box {
                    top: 6px;

                }
            }
        }
    }

    .modal-header {
        background: #fff;
        padding: 20px;
        position: relative;
        border-bottom: solid 1px #dcdcdc;
        .center-title {
            text-align: center;
            font-weight: bold;
            color: #000;
            font-size: 14pt;
        }
        .del-box {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background: #999;
            color: #fff;
            text-align: center;
            font-size: 10pt;
            margin: 10px 10px 0 0;
            cursor: pointer;
            position: absolute;
            right: 10px;
            top: 15px;
            line-height: 22px;
        }

    }

    .modal-footer {
        background: #fff;
        padding: 10px 0;
        border-top: solid 1px #dcdcdc;
        .submit-button-box {
            background: #fff;
            display: flex;
            width: 700px;
            margin: auto;
            justify-content: space-between;
            .cancle-button {
                width: 48%;
                border-radius: 0;
                padding: 0;
                background: #999999;
                color: #fff;
                min-height: 60px;
                font-size: 12pt;
            }
            .submit-button {
                width: 48%;
                border-radius: 0;
                padding: 0;
                background: #000;
                color: #fff;
                min-height: 60px;
                font-size: 12pt;

            }
        }
        &.mobile {
            width:100%;
            padding:15px 15px 40px 15px;
            background:none;
            border-top:none;
            .submit-button-box {
                width:100%;
                margin: auto;
                justify-content: space-between;
                .cancle-button {
                    width: 48%;
                    border-radius: 0;
                    padding: 0;
                    background: #999999;
                    color: #fff;
                    min-height: 50px;
                    font-size: 14px;
                }
                .submit-button {
                    width:100%;
                    border-radius: 0;
                    padding: 0;
                    background: #000;
                    color: #fff;
                    min-height: 50px;
                    font-size: 14px;

                }
            }
        }
    }

    .contents {
        background: #fafafa;
        max-height: 500px;
        overflow-y: auto;
        .file-message {
            margin: -22px 0 10px 0;
            font-size: 10pt;
        }
        .field-box {
            padding: 0;
            & > li {
                border-bottom: solid 1px #DCDCDC;
                display: flex;
                padding: 25px 25px 0 25px;
                &:first-child {
                    padding-top: 0;
                    border-top: none;
                }
                .required-icon {
                    width: 150px;
                    color: #000;
                    font-weight: bold;
                    font-size: 12pt;
                    padding-top: 15px;

                }
                .field-input {
                    width: 70%;
                    .switch-ul {
                        width: 100%;
                        padding: 0;
                        display: flex;
                        margin-bottom: 25px;
                        li {
                            background: #fff;
                            width: 50%;
                            border: solid 1px #dcdcdc;
                            text-align: center;
                            line-height: 55px;
                            height: 55px;
                            cursor: pointer;
                            &.active {
                                background: #2D8CFF;
                                color: #fff;
                            }
                            &:first-child {
                                border-right: none;
                                border-radius: 10px 0 0 10px;
                            }
                            &:last-child {
                                border-radius: 0 10px 10px 0;
                            }
                        }
                    }

                }
            }
        }
        .file-box {
            margin: 0 0 25px 0;
            display: flex;
            width: 100%;
            padding: 0;
            min-height: 50px;
            .file-names {
                width: 100%;
                border: solid 1px #9E9E9E;
                border-right: none;
                border-radius: 5px 0 0 5px;
                padding: 15px 0 0 10px;
                i {
                    margin: 3px 0 0 7px;
                    cursor: pointer;
                }

            }
            .file-click {
                position: relative;
                width: 200px;
                background: #fff;
                border: solid 1px #9E9E9E;
                text-align: center;
                color: #000;
                cursor: pointer;
                border-radius: 0 5px 5px 0;
                min-height: 60px;
                padding-top: 18px;
                #vfile, #dfile {
                    opacity: 0;
                    width: 100%;
                    position: absolute;
                    right: 0;
                    min-height: 60px;

                    z-index: 2;
                }
                i {
                    position: relative;
                    top: 2px;
                    font-size: 15pt;
                    margin-right: 10px;

                }
            }
        }
        &.mobile {
            width: 100%;
            padding:10px 15px;
            .file-message {
                margin: -22px 0 10px 0;
                font-size: 10pt;
            }
            .field-box {
                width:100%;
                padding: 0;
                & > li {
                    display:block;
                    padding:0 15px 0 15px;
                    .required-icon {
                        width:100%;
                        color: #000;
                        font-weight: bold;
                        font-size: 12px;
                        padding:10px 0;

                    }
                    .field-input {
                        width:100%;

                        .switch-ul {
                            width: 100%;
                            padding: 0;
                            display: flex;
                            margin-bottom: 25px;
                            li {
                                background: #fff;
                                width: 50%;
                                border: solid 1px #dcdcdc;
                                text-align: center;
                                line-height: 40px;
                                height: 40px;
                                cursor: pointer;
                                &.active {
                                    background: #2D8CFF;
                                    color: #fff;
                                }
                                &:first-child {
                                    border-right: none;
                                    border-radius: 10px 0 0 10px;
                                }
                                &:last-child {
                                    border-radius: 0 10px 10px 0;
                                }
                            }
                        }

                    }
                }
            }
            .file-box {
                margin: 0 0 25px 0;
                display: flex;
                width: 100%;
                padding: 0;
                min-height: 45px;
                .file-names {
                    width: 100%;
                    border: solid 1px #9E9E9E;
                    border-right: none;
                    border-radius: 5px 0 0 5px;
                    padding: 10px 0 0 10px;
                    font-size:12px;
                    i {
                        margin: 3px 0 0 7px;
                        cursor: pointer;
                    }

                }
                .file-click {
                    position: relative;
                    width: 100px;
                    background: #fff;
                    border: solid 1px #9E9E9E;
                    text-align: center;
                    color: #000;
                    cursor: pointer;
                    border-radius: 0 5px 5px 0;
                    min-height: 45px;
                    font-size:14px;
                    padding-top: 14px;
                    #vfile, #dfile {
                        opacity: 0;
                        width: 100%;
                        position: absolute;
                        right: 0;
                        min-height: 45px;
                        z-index: 2;
                    }
                    i {
                        position: relative;
                        top: 2px;
                        font-size: 15pt;
                        margin-right: 10px;

                    }
                }
            }

        }

    }

    .live {
        .contents {
            height: 550px;
            max-height: 550px;
            overflow-y: auto;
            .field-box {
                & > li {
                    &:first-child {
                        padding-top: 30px;
                        border-top: none;
                    }

                }
            }
        }
    }


</style>
